/* The container must be positioned relative: */
.custom-select {
  position: relative;
  font-family: Arial;
  border: 1px solid #CBCBCB;
  width: 260px;
  height: 50px; }

.custom-select select {
  display: none;
  /*hide original SELECT element: */ }

.select-selected {
  background: #FFFFFF;
  border: 1px solid #CBCBCB;
  height: 50px; }

/* Style the arrow inside the select element: */
.select-selected:after {
  position: absolute;
  content: "";
  top: 22px;
  right: 10px;
  width: 0;
  height: 0;
  border: 6px solid transparent;
  border-color: #3C3C3B transparent transparent transparent; }

/* Point the arrow upwards when the select box is open (active): */
.select-selected.select-arrow-active:after {
  border-color: transparent transparent #3C3C3B transparent;
  top: 7px; }

/* style the items (options), including the selected item: */
.select-items div, .select-selected {
  color: #3C3C3B;
  padding: 14px 16px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer; }

/* Style items (options): */
.select-items {
  position: absolute;
  background: #FFFFFF;
  border: 1px solid #CBCBCB;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99; }

/* Hide the items when the select box is closed: */
.select-hide {
  display: none; }

.select-items div:hover, .same-as-selected {
  background-color: rgba(0, 0, 0, 0.1); }

.custom-select-img {
  margin: 30px 0 30px;
  width: 140px !important; }
  .custom-select-img .select-selected {
    padding: 6px 16px; }

.custom-select-top {
  width: 335px;
  background: #EDEDED; }
  .custom-select-top .select-selected {
    background: #EDEDED;
    color: #213564; }
  .custom-select-top .select-items {
    color: #213564; }

.custom-select-wrap {
  display: none;
  justify-content: center;
  margin: 30px 0 20px; }
  @media all and (max-width: 500px) {
    .custom-select-wrap {
